    studyTypeInfo(val, type){
      if (type == 'get'){
        let arr = studyType(type, val);
        let arrItem = [];
        for (let item in arr){
          arrItem.push({
            label: arr[item].name,
            value: arr[item].code
          });
        }
        return arrItem;
      }else {
        let obj = studyType(type, val);
        return obj ? obj.name : '--';
      }
    },
    studentTeachStatusInfo(val, type){
      if (type == 'get'){
        let arr = studentTeachStatus(type, val);
        let arrItem = [];
        for (let item in arr){
          arrItem.push({
            label: arr[item],
            value: item
          });
        }
        return arrItem;
      }else {
        return studentTeachStatus(type, val);
      }
    },
